body.fixed {
	overflow: hidden;
    position: fixed;
}
.bg-dimmed {
	@include Hide_txt;
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: {
		color: #000;
	}
	opacity: 0.5;
	z-index: 50;
}
.layer {
	&-wrap {
		line-height: 1;
		z-index: 60;
	}

	// 
	&-slide {
		opacity: 0;
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: translateY(120%);
		transition: transform .4s;
		box-sizing: border-box;
		&.active {
			transform: translateY(0);
		}
		.slide-top {
			background-color: #fff;
			border-radius: 2rem 2rem 0 0;
			.layer-slide-close {
				cursor: pointer;
				width: 100%;
				height: 2.3rem;
				text-align:center;
				@include Hide_txt;
				&:after {
					content: '';
					@include Vertical-inline;
					width: 4.1rem;
					height: .3rem;
					background-color: #EBEBEB;
					border-radius: .5rem;
				}
			}
			h4 {
				font-size: 1.5rem;
				margin: 0 1.7rem;
				padding: .5rem 0 1.7rem;
				border-bottom: 1px solid #EBEBEB;
				@include Bold;
			}
		}
		.slide-inner {
			background-color: #fff;
			overflow-y: visible;
			overflow-x: hidden;
			max-height:calc(100vh - 11rem);
			.slide-cont {
				padding: 0 1.7rem;
			}
			.slide-btn {
			}
		}

		// 
		&.layer-filter {
			.filter-reset-btn {
				@include Position($top:2rem, $right:1.7rem);
				i {margin-right: .4rem;}
			}
			.filter-col {
				position: relative;
				padding-top: 2rem;
				h5 {
					font-size: 1.4rem;
					@include Bold;
				}
				.price-all {
					border-bottom:1px solid #2B2B2B;
					@include Position($top: 2rem, $right: 0);
				}
				.chk-list {
					margin-top: .4rem;
					li {
						input {
							display: none;
						}
						label {
							position: relative;
							height: 4.1rem;
							display: block;
							@include Align-middle;
							box-sizing: border-box;
							font-size: 1.2rem;
							border-radius: 1rem;
							border: .15rem solid #EBEBEB;
							padding: 0 1.2rem;
							transition: all .4s;
						}
						input:checked + label {
							border-color: #2B2B2B;
							@include Bold;
							&:after {
								display: block;
								content: '';
								@include Position( $top: .7rem, $right: .7rem);
								z-index: 1;
								width: .5rem;
								height: .5rem;
								margin-left: -.2rem;
								border-radius: 100%;
								background-color: #F04040;
							}
						}
					}
				}
				.price_box_case {
					position: relative;
					&:before {
						display: block;
						content: '';
						width: 100%;
						width: calc(100% - 1.3rem);;
						height: .5rem;
						background-color: #EBEBEB;
						border-radius: .5rem;
						margin: 1.7rem 0;
					}
					.price-box {
						@include Position($top: 0, $right: 0);
						width: 100%;
						.ui-widget.ui-widget-content {
							background-color: #EBEBEB;
							border:0;
							height: .5rem;
							margin: 0 2.5rem 0 .8rem;
						}
						.ui-slider .ui-slider-range {
							background-color: #f04040;
							height: .5rem;
						}
						.ui-slider-horizontal .ui-slider-handle {
							width: 1.7rem;
							height: 1.7rem;
							background-color: #fff;
							border-radius: 100%;
							top: -.7rem;
							border: 0;
							box-shadow: 0 1px 5px rgba(96,97,112,0.3);
						}
					}
					.price-txt {
						@include Align-between;
						span {
							position: relative;
							&:first-child {
								margin-right: 1rem;
							}
							&:after {
								display: block;
								content: '';
								@include Position($top:-2.05rem, $left:50%);
								background-color: #fff;
								width: .2rem;
								height: .2rem;
								border-radius: 100%;
								margin-left: -.1rem;
							}
							&.active {
								@include Bold;
							}
						}
					}
				}

			}
			.slide-btn {
				padding: 2.6rem 1.7rem;
			}
		}

	}

	// 
	&-popup {
		position: fixed;
		width:100%;
		max-width:30rem;
		display: none;
		.pop-inner {
			overflow: hidden;
			border-radius:0.625rem;
			background-color:#f4f4f4;
			margin:0.9375rem;
			position: relative;
		}
		.pop-cont {
			padding:4.0625rem 1.25rem 2.5rem;position: relative;-webkit-box-sizing:border-box;box-sizing:border-box;
		}
	}
	
}